.checkoutProduct {
	display: flex;
	margin-top: 20px;
	margin-bottom: 20px;

	background-color: white;
}

.checkoutProduct__info {
	padding-left: 20px;
}

.checkoutProduct__info > button {
	border-radius: 12px;

	background: #f0c14b;
	border: 1px solid;
	margin-top: 10px;
	border-color: #a88734 #9c7e31 #846a29;
	color: #111;
}

.checkoutProduct__info > button:hover {
	background: #111;
	border: 2px solid;
	margin-top: 10px;
	border-color: #f0c14b;
	color: #f0c14b;
}

.checkoutProduct__image {
	object-fit: contain;
	width: 180px;
	height: 180px;
}

.checkoutProduct__rating {
	display: flex;
}

.checkoutProduct__title {
	font-size: 17px;
	font-weight: 800;
}
